/** Ui **/
header {
  height: 8rem;
  width: 100%;
  background-color: rgba(6, 65, 124, 0.5);
  backdrop-filter: blur(1rem);
  display: flex;
  align-items: center;
  justify-content: left;
  position: fixed;
  z-index: 200;
  top: 0;
}
header .logo {
  height: 8rem;
  margin: 0rem 2rem;
}
header .logo img {
  max-height: 100%;
  max-width: 100%;
}
header .icon_menu {
  width: 3rem;
  height: 100%;
  display: none;
  justify-content: center;
  align-items: center;
}
header .icon_menu i {
  font-size: 2.7rem;
  cursor: pointer;
  position: absolute;
}
header .menu {
  width: 100%;
  display: flex;
  justify-content: center;
  transition: all 300ms cubic-bezier(0.785, 0.135, 0.15, 0.86);
}
header .options {
  display: flex;
}
header .options .activ {
  background-color: #414656;
  border: 0.1rem solid #A6ABBD;
  border-radius: 0.5rem;
  backdrop-filter: blur(1rem);
  transition: color 300ms;
}
header .options .activ .option p {
  color: #fff;
  font-size: 2rem;
  font-weight: 700;
}
header .options .option {
  padding-left: 2rem;
  padding-right: 2rem;
  display: flex;
  align-items: center;
  height: 4rem;
}
header .options .option p {
  color: #fff;
  cursor: pointer;
}
header .options .option:hover {
  background-color: #414656;
  border: 0.1rem solid #A6ABBD;
  border-radius: 0.5rem;
}
header .options .option:hover p {
  color: #fff;
  font-weight: 700;
}

@media (max-width: 480px) {
  header {
    padding: 0 1rem;
    justify-content: space-between;
  }
  header .menu {
    height: 0vh;
    width: 100%;
    position: absolute;
    top: 80px;
    left: 0;
    right: 0;
    background: rgba(65, 70, 86, 0.9);
    backdrop-filter: blur(1rem);
    transition: 1.5s;
    overflow: hidden;
  }
  header .menu .options {
    display: block;
    margin: 15vh auto 0 auto;
    text-align: center;
  }
  header .menu .options .activ .option {
    height: 5rem;
    background-color: #06417c;
    border-radius: 0.5rem;
    backdrop-filter: blur(1rem);
    transition: color 100ms;
  }
  header .menu .options .activ .option p {
    margin: 0;
    font-size: 3.5rem;
  }
  header .menu .options .option {
    height: 5rem;
    font-size: 3.5rem;
    width: 100%;
    margin: 2.5rem 0;
  }
  header .menu .options .option:hover {
    background-color: #06417c;
    border: 0.1rem solid #A6ABBD;
  }
  header .active {
    opacity: 1;
    transition: 1.5s;
    height: 100vh;
  }
  header .icon_menu {
    color: #fff;
    display: inline-flex;
  }
}
footer {
  background-color: #A6ABBD;
  width: 100%;
  height: 8rem;
  margin-top: 5rem;
}
footer p {
  margin-left: 15rem;
}

@media (max-width: 811px) {
  footer {
    height: 10rem;
  }
  footer p {
    margin-left: 2rem;
  }
