.hero-index {
  width: 100vw;
  height: 50vh;
  background-image: url(/build/img/index-hero.jpg);
  background-attachment: fixed;
  background-position: center;
  background-size: cover;
  position: relative;
  overflow: hidden;
  text-align: center;
}
.hero-index .color {
  padding-top: 16rem;
  width: 100%;
