.container-items {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
}
.container-items .item {
  padding-top: 0.5rem;
  text-align: center;
  align-items: center;
  background-color: #A6ABBD;
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.3);
  border-radius: 1rem;
}
.container-items .item .proTitle, .container-items .item .price {
  font-weight: 700;
}
.container-items .item img {
  border-radius: 0.5rem;
  width: 25rem;
  height: 25rem;
}
.container-items .item:hover {
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.5);
  transform: scale(1.1);
}
.container-items .info-product {
  padding: 1.5rem 3rem;
  line-height: 1;
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.container-items .info-product .price {
  font-size: 18px;
  font-weight: 900;
}

.modal-container {
  width: 90%;
  margin: 0 5%;
  max-height: 100%;
  color: #fff;
  background-color: #A6ABBD;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: absolute;
  overflow-y: scroll;
  top: 8rem;
  scrollbar-color: none;
}
.modal-container .modal-header {
  display: flex;
  justify-content: space-between;
  background-color: #414656;
  align-items: center;
  color: #fff;
  width: 100%;
}
.modal-container .modal-header .modal-header-title {
  color: #fff;
  justify-content: start;
  margin: 0 0 0 5rem;
}
.modal-container .modal-content {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  padding: 1rem;
  border-bottom: 0.1rem solid #06417c;
}
.modal-container img {
  height: 10rem;
  width: 10rem;
}
.modal-container .total-content {
  display: flex;
  justify-content: space-around;
  width: 100%;
  background-color: #979db2;
  font-size: 2.5rem;
  text-align: center;
  align-items: center;
}

.icon_carrito .cantidad-carrito {
  height: 3rem;
  width: 3rem;
  background-color: rgba(255, 255, 255, 0.5);
  color: #000;
  font-weight: 700;
  border-radius: 50%;
  font-size: 1.5rem;
  padding: 1rem;
  top: 4rem;
  right: 1.4rem;
  position: absolute;
  display: none;
}

.modal {
  opacity: 0;
  visibility: hidden;
  position: fixed;
  z-index: 1000;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  background: rgba(27, 27, 27, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}
.modal .modalForm {
  width: 60%;
  max-height: max-content;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  position: relative;
  padding: 1rem;
  transition: transform 0.8s;
  transform: translateY(0%);
  border-radius: 1rem;
  background-color: #414656;
}
.modal .modalForm h3 {
  margin: 0;
}
.modal .modalForm .btnClose {
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;
  display: inline-block;
  width: 2.5rem;
  height: 2.5rem;
  background-color: red;
  color: #fff;
  line-height: 2.5rem;
  cursor: pointer;
  border-radius: 50%;
  border: none;
}
.modal .modalForm .btnClose:hover {
  transform: scale(1.2);
}
.modal .modalForm .pago, .modal .modalForm .personales {
  margin: 2rem;
  background-color: #A6ABBD;
  border-radius: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.modal .modalForm .pago .inputBox, .modal .modalForm .personales .inputBox {
  margin: 0.5rem;
}
.modal .hiden {
  transform: translateY(-200%);
}

