header {
    height: 8rem;
    width: 100%;
    background-color: $blueT;
    backdrop-filter: blur(1rem);
    display: flex;
    align-items: center;
    justify-content: left;
    position: fixed;
    z-index: 200;
    top: 0;

    .logo {
        height: 8rem;
        margin: 0rem 2rem;

        img {
            max-height: 100%;
            max-width: 100%;
        }
    }

    .icon_menu {
        width: 3rem;
        height: 100%;
        display: none;
        justify-content: center;
        align-items: center;

        i {
            font-size: 2.7rem;
            cursor: pointer;
            position: absolute;
        }
    }

    .menu {
        width: 100%;
        display: flex;
        justify-content: center;
        transition: all 300ms cubic-bezier(0.785, 0.135, 0.15, 0.86);
    }

    .options {
        display: flex;

        .activ {
            .option {
                p {
                    color: $white;
                    font-size: 2rem;
                    font-weight: $black-f;
                }
            }

            background-color: $gray;
            border: .1rem solid $grayC;
            border-radius: .5rem;
            backdrop-filter: blur(1rem);
            transition: color 300ms;
        }
        .option {
            padding-left: 2rem;
            padding-right: 2rem;
            display: flex;
            align-items: center;
            height: 4rem;

            p {
                color: $white;
                cursor: pointer;
            }

            &:hover {
                p {
                    color: $white;
                    font-weight: $black-f;
                }

                background-color: $gray;
                border: .1rem solid $grayC;
                border-radius: .5rem;
            }
        }
    }
}

@include telefono {
    header {
        padding: 0 1rem;
        justify-content: space-between;
        .menu {
            height: 0vh;
            width: 100%;
            position: absolute;
            top: 80px;
            left: 0;
            right: 0;
            background: $grayT;
            backdrop-filter: blur(1rem);
            transition: 1.5s;
            overflow: hidden;
            .options {
                display: block;
                margin: 15vh auto 0 auto;
                text-align: center;
                .activ {
                    .option {
                        height: 5rem;
                        background-color: $blue;
                        border-radius: .5rem;
                        backdrop-filter: blur(1rem);
                        transition: color 100ms;
                        p {
                            margin: 0;
                            font-size: 3.5rem;
                        }
                    }
                }
                .option {
                    height: 5rem;
                    font-size: 3.5rem;
                    width: 100%;
                    margin: 2.5rem 0;
                    &:hover {
                        background-color: $blue;
                        border: .1rem solid $grayC;
                    }
                }
            }
        }
        .active {
            opacity: 1;
            transition: 1.5s;
            height: 100vh;
        }
        .icon_menu {
            color: $white;
            display: inline-flex;
        }
    }
}