.hero-refer {
    width: 100%;
    height: 50vh;
    background-image: url(/build/img/refer-hero.jpg);
    background-attachment: fixed;
    background-position: center;
    background-size: cover;
    position: relative;
    overflow: hidden;
    text-align: center;
    .color {
        padding-top: 16rem;
        width: 100%;
        height: 100%;
        background-color: $blueT;
        .textos {
            h1 {
                color: $white;
                letter-spacing: 3rem;
                font-weight: $black-f;
            }
        }
    }
}
// Cotenido

.referencias{
    display: flex;
    flex-wrap: wrap;
    .testimonial {
        background-color: $gray;
        font-size: 2rem;
        padding: 1rem;
        color: $white;
        border-radius: 2rem;
        margin: 1rem;
        width: 35rem;
        height: 59rem;
        blockquote {
            padding-left: 5rem;
            position: relative;
            &::before {
                content: '';
                background-image: url(/build/img/comilla.svg);
                background-repeat: no-repeat;
                width: 5rem;
                height: 6rem;
                display: block;
                position: absolute;
                left: -2rem;
            }
        }
        p {
            text-align: right;
            color: $white;
        }
        
    }
}
.trabajos {
    display: flex;
    height: auto;
    padding: 1rem 0;
    width: 100%;
    .columna{
        min-width: 32rem;
        margin: 1rem;
        picture{
            img {
                height: auto;
                margin: 1rem ;
                filter: grayscale(100%);
                transition: all .3s ease;
                &:hover{
                    cursor: pointer;
                    filter: grayscale(0%);
                    box-shadow: 0 0 1rem $blackT;
                    transform: scale(1.1);
                }
            }
        }

    }
}
@include tablet {
    .referencias{
        justify-content: center;
        .testimonial {
            width: 50rem;
            height: 37rem;
        }
    }
    .trabajos {
        flex-wrap: wrap;
        .columna{
            width: 100%;
            margin: .5rem;
        }
    }
}
@include telefono {
    .hero-refer {
        .color {
            .textos {
                h1 {
                    font-size: 3rem;
                    letter-spacing: 1.5rem;
                }
            }
        }
    }
    .referencias {
        .testimonial {
            height: 67rem;
            blockquote {
                padding-left: 3rem;
                &::before {
                    content: '';
                    width: 4rem;
                    height: 5rem;
                    left: -2rem;
                }
            }
        }
    }
}
    
