html {
    font-size: 62.5%;
    box-sizing: border-box;   
}
*, *:before, *:after {
    box-sizing: inherit;
}
body {
    font-family: $fuente;
    font-size: 1.8rem;
    line-height: 1.8;
}
p {
    color: $black;
    letter-spacing: .23rem;
}
.contenedor {
    width: 90%;
    max-width: 120rem;
    margin: 0px auto;
}
a {
    text-decoration: none;
}
img{
    width: 100%;
    height: 100%;
}

.icon-cart{
    position: relative;
    width: 40px;
    height: 40px;
    stroke: #FFF;
}
.icon-cart:hover{
    cursor: pointer;
}

h1, h2, h3,h4 {
    color: $black;
    font-family: $fuente;
    font-weight: $black-f;
    text-align: center;
    letter-spacing: .3rem;

}
label {
    color: $white;
    letter-spacing: .2rem;
}
h1 {
    font-size: 5.5rem;
}
h2 {
    font-size: 4.5rem;
}
h3 {
    font-size: 3.5rem;
}
h4 {
    font-size: 1.8rem;
}

@include tablet {
    h1 {
        font-size: 4.5rem;
    }
    h2 {
        font-size: 3.5rem;
    }
    h3 {
        font-size: 2.5rem;
    }
}

@include telefono {
    h1 {
        font-size: 3rem;
    }
    h2 {
        font-size: 2.5rem;
    }
    h3 {
        font-size: 2.2rem;
    }

}
