.hero-serv {
    width: 100vw;
    height: 50vh;
    background-image: url(/build/img/servicios-hero.jpg);
    background-attachment: fixed;
    background-position: center;
    background-size: cover;
    position: relative;
    overflow: hidden;
    text-align: center;
    .color {
        padding-top: 16rem;
        width: 100%;
        height: 100%;
        background-color: $blueT;
        .textos {
            h1,p {
                color: $white;
            }
            h1 {
                letter-spacing: 3rem;
                font-weight: $black-f;
            }
            p {
                letter-spacing: 1rem;
            }
        }
    }
}
//contenido
.container-items{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
    .item{
        padding-top: .5rem;
        text-align: center;
        align-items: center;
        .proTitle,.price {
            font-weight: $black-f;
        }
            img{
                border-radius: .5rem;
                width: 25rem;
                height: 25rem;
            }
        background-color: $grayC;
        box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.3);
        border-radius: 1rem;
        &:hover{
            box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.5);
            transform: scale(1.1);
        }
    }
    .info-product{
        padding: 1.5rem 3rem;
        line-height: 1;
        display: flex;
        flex-direction: column;
        gap: 10px;
        .price{
            font-size: 18px;
            font-weight: 900;
        }
    }
}
// Modal Container
.modal-container {
    width: 90%;
    margin: 0 5%;
    max-height: 100%;
    color: $white;
    background-color: $grayC;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: absolute;
    overflow-y: scroll;
    top: 8rem;
    scrollbar-color: none;
    
    .modal-header {
        display: flex;
        justify-content: space-between;
        background-color: $gray;
        align-items: center;
        color: $white;
        width: 100%;
        .modal-header-title{
            color: $white;
            justify-content: start;
            margin: 0 0 0 5rem;
        }
    }
    .modal-content {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        align-items: center;
        width: 100%;
        padding: 1rem;
        border-bottom: .1rem solid $blue;
    }
    img {
        height: 10rem;
        width: 10rem;
    }
    .total-content {
        display: flex;
        justify-content: space-around;
        width: 100%;
        background-color: darken($grayC, 5);
        font-size: 2.5rem;
        text-align: center;
        align-items: center;
    }
}
.icon_carrito {
    .cantidad-carrito {
        height: 3rem;
        width: 3rem;
        background-color: $whiteT;
        color: $black;
        font-weight: $black-f;
        border-radius: 50%;
        font-size: 1.5rem;
        padding: 1rem;
        top: 4rem;
        right: 1.4rem;
        position: absolute;
        display: none;
    }
}
.modal {
    opacity: 0;
    visibility: hidden;
    position: fixed;
    z-index: 1000;
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
    background: rgba(27, 27, 27, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    .modalForm {
        width: 60%;
        max-height: max-content;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        position: relative;
        padding: 1rem;
        transition: transform .8s;
        transform: translateY(0%);
        border-radius: 1rem;
        background-color: $gray;
        h3 {
            margin: 0;

        }
        .btnClose {
            position: absolute;
            top: .5rem;
            right: .5rem;
            display: inline-block;
            width: 2.5rem;
            height: 2.5rem;
            background-color: red;
            color: #fff;
            line-height: 2.5rem;
            cursor: pointer;
            border-radius: 50%;
            border: none;
        }
        .btnClose:hover{
            transform: scale(1.2);
        }
        .pago,.personales {
            margin: 2rem;
            background-color: $grayC;
            border-radius: 1rem;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            .inputBox{
                margin: .5rem;
            }
        }
    }
    .hiden {
        transform: translateY(-200%);
    }  
}


@include tablet {
    .container-items {
        display: flex;
        flex-wrap: wrap;
        .item {
            width: 32rem;
            margin: 1rem;
        }
    }
    .modalForm {
        width: 90%;
    }
}
@include telefono{
    .icon_carrito {
        .cantidad-carrito {
            height: 3rem;
            width: 3rem;
            background-color: $whiteT;
            color: $black;
            font-weight: $black-f;
            border-radius: 50%;
            font-size: 1.5rem;
            padding: 1rem;
            top: 4rem;
            right: 10rem;
            position: absolute;
            display: none;
        }
    }
    .hero-serv {
        .color {
            .textos {
                h1 {
                font-size: 2.7rem;
                margin-left: 1rem;
                }
            }
        }
    }
    .modal{

        .modalForm {
            width: 90%;
        }
    }
}

