.contenido-centrado {
    max-width: 80rem;
}
.alinear-derecha {
    display: flex;
    justify-content: flex-end;
}
.sombra{
    box-shadow: 1rem 5rem 20rem 1rem rgb(15 15 14 / 90%);
}
.red_sombra{
    box-shadow: 0px 1px 15px 0px rgb(135 17 17 / 80%);
}
.label-b{
  color: $black;
}
.boton {
    display: block;
    font-family: $fuente;
    color: $white;
    text-align: center;
    padding: 1rem 2rem;
    font-size: 1.5rem;
    text-transform: uppercase;
    font-weight: $black-f;
    margin-bottom: 2rem;
    border: none;
    &:hover {
        cursor: pointer;
    }
}
.pading-top {
  padding-top: 8rem;
}
.pading_top_2 {
  padding-top: 11rem;
}
