.hero-somos {
    width: 100%;
    height: 50vh;
    background-image: url(/build/img/somos-hero.jpg);
    background-attachment: fixed;
    background-position: center;
    background-size: cover;
    position: relative;
    overflow: hidden;
    text-align: center;
    .color {
        padding-top: 16rem;
        width: 100%;
        height: 100%;
        background-color: $blueT;
        .textos {
            h1,p {
                color: $white;
            }
            h1 {
                letter-spacing: 3rem;
                font-size: 5.5rem;
                font-weight: $black-f;
            }
            p {
                letter-spacing: 1rem;
                font-size: 2rem;
            }
        }
    }
}
.info {
    picture {
        width: 100%;
        height: 100%;
        margin: 0 1rem;
    }
    .galery-s{
        display: flex; 
        
        img{
            box-shadow: 0 0 1rem $gray;
        }
    }
    .galery-2{
        width: 100%;
        display: flex;
        justify-content: space-between;
            img {
                border-radius: 50% ;
                margin: 1rem;
                box-shadow: 0 0 1rem $gray;
            }
    }
    .info-text {
        margin-top: 5rem;
        text-align: left;
        p{
            margin-bottom: 2.5rem;
        }
    }
}

@include telefono {
    .hero-somos {
        .color {
            .textos {
                h1 {
                    font-size: 3rem;
                    margin-left: 1rem;
                }
            }
        }
    }
    .galery-s,
    .galery-2 {
        flex-wrap: wrap;
    }
}