.hero-contacto {
    width: 100vw;
    height: 50vh;
    background-image: url(/build/img/conato-hero.jpg);
    background-attachment: fixed;
    background-position: center;
    background-size: cover;
    position: relative;
    overflow: hidden;
    text-align: center;
    .color {
        padding-top: 16rem;
        width: 100%;
        height: 100%;
        background-color: $blueT;
        .textos {
            h1 {
                color: $white;
                letter-spacing: 3rem;
                font-weight: $black-f;
            }
        }
    }
}
.info {
    text-align: center;
    justify-content: center;
    .form-cont {
        width: 120rem;
        margin: 1rem;
        .formulario { 
            width: 90%;
            height: 95%;
            padding: 2rem;
            background-color: $grayC;
            border-radius: 1rem;
            .campo {
                display: flex;
                margin-bottom: 2rem;
                gap: 2rem;
                color: $black;
                .campo_label {
                    flex: 0 0 9rem;
                    text-align: right;
                    margin-right: .5rem;
                }
                input{
                    display: block;
                    height: 5rem;
                    width: 100%;
                    background-color: $gray;
                    border-radius: .3rem;
                    padding: 0 1rem;
                    margin-top: .8rem;
                    color: $grayC;
                    &:focus {
                        border: 1px solid $grayC;
                        box-shadow: 0 0 1rem $blue;
                        color: $white;
                    }
                    
                }
                .campo_field {
                    flex: 1;
                    outline: none;
                    border: none;
                }
                .campo_field-textarea {
                    height: 20rem;
                    width: 100%;
                    background-color: $gray;
                    border-radius: .3rem;
                    padding: 0 1rem;
                    margin-top: .8rem;
                    &:focus {
                        border: 1px solid $blackT;
                        box-shadow: 0 0 1rem $blue;
                        color: $white;
                    }
                    &::placeholder{
                        color: $white;
                    }
                }
            }
        }
    }
}
@include tablet {
    .info {
        .form-cont{
            width: 81rem;
            .formulario{
                width: 85%;
            }
        }
    }
}
@include telefono {
    .hero-contacto {
        .color {
            .textos {
                h1 {
                    font-size: 4rem;
                    letter-spacing: 1.5rem;
                }
            }
        }
    }
    .info {
        .form-cont {
            width: 35rem;
            .formulario {
                width: 90%;
            }
        }
    }
}